



























import { Component, Vue } from "vue-property-decorator";
import * as CMS from "@/api/cms.ts";
import Waterfall from "vue-waterfall/lib/waterfall";
import WaterfallSlot from "vue-waterfall/lib/waterfall-slot";
import CoolLightBox from "vue-cool-lightbox";
import "vue-cool-lightbox/dist/vue-cool-lightbox.min.css";

@Component({
  components: { Waterfall, WaterfallSlot, CoolLightBox },
})
export default class Portfolio extends Vue {
  private images = [];
  private lightboxUrls = [];
  private lightboxIndex: number | null = null;

  async mounted(): Promise<void> {
    const res = await CMS.getSchilderijen();
    this.images = res.map((x) => x.afbeelding);
    this.lightboxUrls = res.map((x) => x.afbeelding.url);
  }

  focusImage(index: number): void {
    this.lightboxIndex = index;
  }
}
