import axios from "axios";

const env = process.env;
axios.defaults.baseURL = env.VUE_APP_CMS_BASE_URL;


async function getContent(path: string): Promise<any> {
  return axios.get(path).then(response => response.data);
}

export async function getPages() { return await getContent("/pages"); }
export async function getSchilderijen() { return await getContent("/schilderijens"); }
