




















import { Component, Watch, Vue } from "vue-property-decorator";
import Logo from "@/components/logo.vue";
import Page from "@/components/page.vue";
import Navigation from "@/components/navigation.vue";
import Portfolio from "@/views/Portfolio.vue";
import * as CMS from "@/api/cms";

@Component({
  components: {
    Logo,
    Navigation,
    Page,
    Portfolio,
  },
})
export default class App extends Vue {
  private navPages: Array<string> = [];
  private pages: Array<unknown> = [];
  private overlayHeight = 0;

  async mounted(): Promise<void> {
    this.pages = await CMS.getPages();
    this.navPages = ["portfolio", ...this.pages.sort((a: any, b: any) => a.id - b.id).map((x: any) => x.naam)];
    this.$nextTick(() => this.updateOverlayHeight());
  }

  created(): void {
    window.addEventListener("resize", this.updateOverlayHeight);
  }

  destroyed(): void {
    window.removeEventListener("resize", this.updateOverlayHeight);
  }

  updateOverlayHeight(): void {
    this.overlayHeight = this.$refs.overlay?.offsetHeight;
  }

  @Watch("$store.state.activePageIndex")
  goto(index: number): void {
    if (index == -1) return;
    var element = this.$refs.pages?.children[index];
    var top = element.offsetTop - this.overlayHeight;
    window.scrollTo({ left: 0, top: top, behavior: "smooth" });
  }
}
